<template>
    <div class="content-wrapper onePage">
        <div class="content-scroll">
            <div class="content-scroll-padding-box">
                <div class="menu-list-value-input">
                    <div class="menu" @click="gotoMenu('/locationSharingStatus')">   </div>
                    <div class="menu" @click="gotoMenu('/')">  </div>
                    <div class="menu" @click="gotoMenu('/createSafezone')"> </div>
                    <div class="menu" @click="gotoMenu('/inactiveSensing')">   </div>
                    <div class="menu" @click="gotoMenu('/batteryAlert')">   </div>
                    <div class="menu" @click="gotoMenu('/sosSetting')">SOS </div>
                    <div class="menu" @click="leaveGroup"> </div>
                </div>
            </div>
        </div>
        
        <!-- moadl -->
        <a-modal modal v-model:open="ModalShow" title="">
            <p class="modal-content">
                {{msg[type]}}
            </p>
            <template #footer>
                <div class="btn-set horizontal">
                    <a-button class="width100per" @click="modalClose">{{type === 0 ? '' : ''}}</a-button>
                    <a-button class="width100per" @click="leaveCheckGroup" v-if="type === 0"></a-button>
                </div>
            </template>
        </a-modal>
    </div>
</template>
<script setup>
    import { ref } from 'vue'
    import { useRouter } from 'vue-router'

    const msg = ref([" [1]  ?","[1]  ."])
    const type = ref(0);
    const router = useRouter();
    const gotoMenu = (path)=>{
        router.push(path);
    }
    const leaveGroup = ()=>{
        type.value = 0;
        ModalShow.value = true;
    }
    const ModalShow = ref(false);
    const modalClose = ()=>{
        ModalShow.value = false;
        setTimeout(()=>{
            if(type.value === 1) type.value = 0;
        },500)
    }
    const leaveCheckGroup = ()=>{
        ModalShow.value = false;
        setTimeout(()=>{
            type.value = 1;
            ModalShow.value = true;
            
        },500)
    }
</script>
